<template>
    <div class="projectionvideo" id="projectionvideo">
      <transition appear name="fadein" >
        <div class="controls" v-show="showControls">
        <div class="playarea">
          <img src="../assets/Fast Backward.svg" class="speedup"/>
          <img src="../assets/Play button.svg" class="play" id="play"/>
          <img src="../assets/Fast forward.svg" class="speedup"/>
        </div>
        <div class="bar">
          <div class="line"></div>
        </div>
        <div class="volume">
          <img src="../assets/volumeicon.svg" class="volumebars"/>
          <div class="line"></div>
        </div>
        </div>
      </transition>
     <transition appear name="fadein" :key="srcUrl">
        <video controls controlsList="nodownload" autoplay class="video" id="video" ref="video" :key="srcUrl">
          <source :src="srcUrl" type="video/mp4">
        </video>
     </transition>
    </div>
  </template>
  
  <script>
  export default {
    name: 'ProjectionVideo',
    components: {
    },
    data() {
      return {
        showControls: false,
        timeout: null,
        srcUrl: require("../assets/1.mp4"),
        counter: 1
      }
    },
    computed: [],
    methods: {
      mousemove() {
        clearTimeout(this.timeout);
        this.showControls = true;
        this.timeout = setTimeout(() => {
          this.showControls = false;
          clearTimeout(this.timeout);
        }, 3000);
      },
      ended() {
        
        this.$nextTick(() => {
          switch(this.counter) {
            case 1: {
              this.srcUrl = require("../assets/2.mp4");
              this.counter++;
              this.$nextTick(() => {
                this.$refs.video.play();
                this.$refs.video.removeEventListener("ended", this.ended);
                this.$refs.video.addEventListener("ended", this.ended);
              })
              break;
            }
            case 2: {
              this.srcUrl = require("../assets/3.mp4");
              this.counter++;
              this.$nextTick(() => {
                this.$refs.video.play();
                this.$refs.video.removeEventListener("ended", this.ended);
                this.$refs.video.addEventListener("ended", this.ended);
              })
              break;
            }
            case 3: {
              this.srcUrl = require("../assets/4.mp4");
              this.counter++;
              this.$nextTick(() => {
                this.$refs.video.play();
                this.$refs.video.removeEventListener("ended", this.ended);
                this.$refs.video.addEventListener("ended", this.ended);
              })
              break;
            }
            case 4: {
              this.srcUrl = require("../assets/5.mp4");
              this.counter++;
              this.$nextTick(() => {
                this.$refs.video.play();
                this.$refs.video.removeEventListener("ended", this.ended);
                this.$refs.video.addEventListener("ended", this.ended);
              })
              break;
            }
            case 5: {
              window.location.href = "/";
              break;
            }
          }
        })
      },
      clickEvents(e) {
        console.log(e.target.id);
        switch(e.target.id) {
          case "play": {
            this.$refs.video.play();
            break;
          }
        }
      }
    },
    mounted() {
      // document.getElementById("projectionvideo").addEventListener("click", this.clickEvents)
      // document.getElementById("projectionvideo").addEventListener("mousemove", this.mousemove)
      this.$refs.video.addEventListener("ended", this.ended);
    }
  }
  </script>
  
